<template>
    <div id="zero-days" class="card mb-4 animate__animated animate__fadeIn">
      <div class="card-header">
        <h2>Zero-Day</h2>
      </div>
      <div class="card-body">
        <p class="text-muted">El conocimiento de los “zero days” o vulnerabilidades de día cero es fundamental en el ámbito de la ciberseguridad. Estas vulnerabilidades son fallos de seguridad en software o hardware que son desconocidos para los fabricantes y, por lo tanto, no tienen un parche o solución disponible en el momento de su descubrimiento
        </p>
        
        <div v-if="zeroDays.length === 0" class="text-center py-4">
          <p>No se han reportado zero-days este período</p>
        </div>
        
        <div v-else>
          <table class="table table-striped">
            <thead>
              <tr>
                <th>ID</th>
                <th>Nombre</th>
                <th>Plataforma</th>
                <th>Fecha Detección</th>
                <th>Estado</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(zeroDay, index) in zeroDays" :key="index">
                <td>{{ zeroDay.id || 'N/A' }}</td>
                <td>
                  <a v-if="zeroDay.link" :href="zeroDay.link" target="_blank" rel="noopener noreferrer">
                    {{ zeroDay.nombre }}
                  </a>
                  <span v-else>{{ zeroDay.nombre }}</span>
                </td>
                <td>{{ zeroDay.plataforma }}</td>
                <td>{{ zeroDay.fecha }}</td>
                <td>
                  <span class="badge bg-danger" v-if="zeroDay.activo">Activo</span>
                  <span class="badge bg-secondary" v-else>Mitigado</span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        
        <div class="alert alert-secondary mt-3">
          <em>Clasificación: TLP:CLEAR | La información contenida en este informe puede ser compartida sin restricción.</em>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      zeroDays: {
        type: Array,
        default: () => []
      }
    }
  }
  </script>
  
  <style scoped>
  .table {
    width: 100%;
    margin-bottom: 1rem;
    color: var(--text-color);
    border-collapse: collapse;
  }
  
  .table th,
  .table td {
    padding: 0.75rem;
    vertical-align: top;
    border-top: 1px solid var(--border-color);
  }
  
  .table thead th {
    vertical-align: bottom;
    border-bottom: 2px solid var(--border-color);
  }
  
  .table-striped tbody tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.05);
  }
  
  .dark-mode .table-striped tbody tr:nth-of-type(odd) {
    background-color: rgba(255, 255, 255, 0.05);
  }
  
  .alert-warning {
    background-color: rgba(255, 193, 7, 0.2);
    border-color: rgba(255, 193, 7, 0.3);
    color: var(--warning-color);
  }
  
  .badge {
    padding: 0.35em 0.65em;
    font-size: 0.75em;
    font-weight: 700;
    border-radius: 0.25rem;
  }
  
  .bg-danger {
    background-color: var(--danger-color);
    color: white;
  }
  
  .bg-secondary {
    background-color: var(--secondary-color);
    color: white;
